.hero-main{
    background: url('../../aasets/imgase/mm.jpg') no-repeat top right/cover;
    background-color: var(--color-dark);
    color: var(--color-light);
    border-radius: var(--border-redius-primary);
    padding: var(--padding-primary);
}
.hero-text{
    max-width: 500px;
}

.hero-title{
    font-size: 60px;
    text-transform: uppercase;
    margin-bottom: calc(var(--margin-primary)/2);
    font-weight: 900px;
}
.hero-title em{
    font-size: normal;
    color: var(--color-primary)
}
.hero-subtitle{
    font-size: 20px;
    color:white;
    font-weight: var(--font-weight-bold);
    margin-bottom:  calc(var(--margin-primary)/2);
}
